.catalog {
  display: flex;
  flex-direction: column;
}

.catalog--main {
  .container-responsive;

  @media @mobile-only {
    width: auto;
    padding: 0;
  }

  @media @tablet {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

