.catalog__main-title {
  box-sizing: border-box;
  width: 100%;
  margin: 0;

  font-weight: normal;
  font-size: 36px;
  line-height: 1;
  color: @cod-gray;

  background-color: @white;

  @media @mobile-only {
    padding: 27px 20px 40px;

    border-top: 1px solid @gallery;
  }

  @media @tablet {
    font-size: 60px;
  }
}

.catalog__secondary-title {
  position: relative;

  margin: 0;

  font-weight: normal;
  font-size: 24px;
  line-height: 1;
  color: @cod-gray;

  span {
    position: relative;

    display: inline-block;
    order: 10;
    margin-bottom: 34px;
    padding-right: 20px;

    vertical-align: center;

    background-color: @white;

    @media @tablet {
      margin-left: -40px;
      padding-right: 40px;
      padding-left: 40px;
    }
  }

  @media @tablet {
    font-size: 36px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 0;

    width: 100%;
    height: 3px;

    background-color: @apple;

    @media @mobile-only {
      left: 20px;
    }

    @media @tablet {
      top: 30px;
    }
  }
}

.catalog__item-main {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  margin-bottom: 24px;
  padding: 0 20px;

  @media @mobile-only {
    border-top: 1px solid @gallery;
  }

  @media @tablet {
    width: 45%;
    margin-top: 140px;
    padding: 165px 37px 40px 38px;

    background-color: @concrete;

    &:hover,
    &:focus {
      box-shadow: 0 10px 25px 0 rgba(41, 52, 73, 0.5);
    }
  }

  @media @desktop {
    width: 245px;
    margin-right: 80px;

    &:nth-of-type(2n) {
      margin-right: 80px;
    }

    &:nth-of-type(4n) {
      margin-right: 0;
    }
  }
}

.catalog__item-main--to-all {
  margin-bottom: 28px;
  padding: 80px 20px 25px;

  background-color: @white;

  @media @mobile-only {
    border-bottom: 1px solid @gallery;
  }

  @media @tablet {
    border: 1px solid @gallery;
  }

  &::before {
    content: "";
    position: absolute;
    top: 27px;
    left: 50%;

    width: 31px;
    height: 31px;

    background-image: url("../img/icon-plus-mobile.svg");
    background-repeat: no-repeat;
    transform: translateX(-16px);
  }
}

.catalog__item-secondary {
  @media @mobile-only {
    padding: 11px 0 17px;

    border-bottom: 1px solid @silver;
  }

  @media @tablet {
    padding: 28px 38px;

    background-color: @white;
    border: 1px solid @gallery;
    border-top: none;

    &:first-of-type {
      border-top: 1px solid @gallery;
    }
  }

  @media @desktop {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
  }
}

.catalog__wrapper {
  display: flex;
  flex-direction: row;
}

.catalog__description {
  flex-grow: 1;
  padding: 20px 0;


  &--secondary {
    margin-bottom: 15px;
    padding: 0;

    @media @tablet {
      flex-direction: row;
      align-items: center;
      margin: 0;
    }

    @media @desktop {
      justify-content: left;
    }
  }
}

.catalog__title {
  margin: 0 0 13px;

  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: @cod-gray;
  text-transform: uppercase;

  &--to-all {
    margin: 0 0 5px;

    text-align: center;
  }

  @media @tablet {
    font-size: 20px;
  }

  &--secondary {
    margin: 0 0 11px;

    @media @tablet {
      margin: 0;
    }

    @media @desktop {
      width: 50%;
    }
  }
}

.catalog__image-link {
  font-size: 0;
}

.catalog__link {
  display: flex;
  flex-direction: column;

  color: @cod-gray;
  text-decoration: none;
}

.catalog__image {
  width: 140px;
  height: 144px;

  @media @tablet {
    position: absolute;
    left: 50%;

    width: 324px;
    height: auto;

    transform: translateX(-162px);

    &--big {
      top: -80px;
    }

    &--small {
      top: -45px;
    }
  }

  @media @desktop {
    width: 245px;

    transform: translateX(-123px);
  }
}

.catalog__features-list {
  width: 100%;

  border-collapse: collapse;
  font-size: 12px;
  line-height: 16px;
  font-family: @font-secondary;
  color: @tundora;

  @media @desktop {
    font-size: 16px;
  }

  &--secondary {
    @media @desktop {
      width: 50%;
    }
  }
}

.catalog__features-item {
  display: flex;
  justify-content: space-between;

  th {
    padding: 0;

    font-weight: normal;
  }

  td {
    padding: 0;
  }

  &--secondary {
    font-size: 14px;
    line-height: 1;

    @media @desktop {
      justify-content: left;
    }

    td {
      @media @tablet {
        margin-left: 50px;

        font-size: 16px;
      }

      @media @desktop {
        margin: 0;

        &:first-child {
          width: 70%;
        }
      }
    }
  }
}

.catalog__text {
  margin: 0 0 20px;

  font-size: 12px;
  font-family: @font-secondary;
  text-align: center;
  color: @tundora;

  @media @desktop {
    font-size: 16px;
  }
}

.catalog__container {
  width: 100%;

  .container-responsive;

  @media @desktop {
    display: flex;
  }
}

.catalog__subcontainer {
  @media @mobile-only {
    border-top: 1px solid @silver;
  }

  @media @desktop {
    flex-grow: 1;
  }
}

.catalog__button {
  &--secondary {
    @media @tablet {
      margin-top: 25px;
    }

    @media @desktop {
      width: 245px;
      margin: 0;
    }
  }
}
